import React from 'react';
import {
  InfoRightBottMobile,
  InfoRightBottom1,
  InfoRightBottom2,
  InfoRightTop1,
  InfoRightTop2,
  MainContainer,
  MainH2,
  MainH2Container,
  MainH2Other,
  MainH2Text,
  MainIcon,
  MainIconImg,
  MainInfoContainer,
  MainInfoIcon1,
  MainInfoIcon2,
  MainInfoRight,
  MainInfoWrapp,
  MainLeft,
  MainText,
  MainTextEnd,
  MainTextEndDesktop,
  MainTextEndMobile,
  MainTitle,
} from './MainContent.styled';
import mainIconImg from '../../assets/gg_time (1).png';
import mainInfoImg1 from '../../assets/Group3880.png';
import mainInfoImg2 from '../../assets/Group4018.png';
import Form from '../Form/Form';

function MainContent() {
  return (
    <MainContainer>
      <MainLeft>
        <MainIcon>
          <MainIconImg src={mainIconImg} alt="MainIconImg" />
          
        </MainIcon>
        <MainTitle>front-end</MainTitle>
        <MainH2Container>
          <MainH2>
            <MainH2Text> </MainH2Text>
          </MainH2>
          <MainH2Other>   IT </MainH2Other>
        </MainH2Container>
        <MainText>
           
          <b>       2022  </b>
               
          <br />
        </MainText>
        <MainTextEndDesktop> </MainTextEndDesktop>
        <MainTextEndMobile>   1000$</MainTextEndMobile>
        <MainTextEnd> 1000$</MainTextEnd>
        <Form mobile="true" />
        <MainInfoWrapp>
          <MainInfoContainer>
            <MainInfoIcon1 src={mainInfoImg1} alt="infoIcon1" />
            <MainInfoRight>
              <InfoRightTop1>
                 <span style={{ color: '#4252D1' }}></span>
              </InfoRightTop1>
              <InfoRightBottom1>
                6       
                <br /> Mercedes-Benz    
              </InfoRightBottom1>
            </MainInfoRight>
          </MainInfoContainer>
          <MainInfoContainer>
            <MainInfoIcon2 src={mainInfoImg2} alt="infoIcon2" />
            <MainInfoRight>
              <InfoRightTop2>  </InfoRightTop2>
              <InfoRightBottom2>
                PDF- "5 
                <br />   "
              </InfoRightBottom2>
              <InfoRightBottMobile>
                PDF- "5 
                <br />  -"
              </InfoRightBottMobile>
            </MainInfoRight>
          </MainInfoContainer>
        </MainInfoWrapp>
      </MainLeft>
      <Form mobile="false" />
    </MainContainer>
  );
}

export default MainContent;
